import React, { useState } from "react";
import Bars from "./bars.jsx";
function Sortvis() {
  var [size, setSize] = new useState(100);
  var [isSorting, setSorting] = new useState(false);

  function handleChange(event) {
    setSize(event.target.value);
    console.log(size);
    Generate();
  }

  function waitforme(milisec) {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve("");
      }, milisec);
    });
  }

  var a = [];
  for (var i = 0; i < size; i++) {
    a.push(getRandomInt(10, 100));
  }
  var [arr, setArr] = new useState(a);
  function Generate() {
    setArr(function () {
      var newArr = [];
      for (var i = 0; i < size; i++) {
        newArr.push(getRandomInt(10, 100));
      }
      return newArr;
    });
  }

  function inputarray(ele, index) {
    return <Bars key={index} height={ele * 5} number={ele} />;
  }
  async function Insertionsort() {
    setSorting(true);
    var tot = document.getElementsByClassName("Bars");
    for (var i = 1; i < arr.length; i++) {
      for (var j = i; j >= 1; j--) {
        if (arr[j - 1] <= arr[j]) {
          break;
        } else {
          var first = arr[j - 1];
          arr[j - 1] = arr[j];
          arr[j] = first;
          tot[j - 1].style["background-color"] = "blue";
          tot[j].style["background-color"] = "blue";
          await waitforme(j);
          tot[j - 1].style["height"] = arr[j - 1] * 5 + "px";
          tot[j].style["height"] = arr[j] * 5 + "px";
          await waitforme(j);
          tot[j - 1].style["background-color"] = "brown";
          tot[j].style["background-color"] = "brown";
        }
      }
    }
    setSorting(false);
  }

  async function BubbleSort() {
    setSorting(true);
    var tot = document.getElementsByClassName("Bars");
    for (var i = 0; i < arr.length; i++) {
      var max = 0;
      await waitforme(5);
      tot[0].style["backgroung-color"] = "blue";

      for (var j = 0; j < arr.length - i; j++) {
        if (arr[j] > arr[max]) {
          await waitforme(10);
          tot[max].style["background-color"] = "brown";
          max = j;
          tot[max].style["background-color"] = "blue";
        }
      }
      var temp = arr[max];
      arr[max] = arr[arr.length - i - 1];
      arr[arr.length - i - 1] = temp;
      await waitforme(i);
      tot[max].style["height"] = arr[max] * 5 + "px";
      tot[arr.length - i - 1].style["height"] =
        arr[arr.length - i - 1] * 5 + "px";
      await waitforme(40);
      tot[max].style["background-color"] = "brown";
    }
    setSorting(false);
  }

  async function QuickSort() {
    setSorting(true);
    // Creating an array that we'll use as a stack, using the push() and pop() functions
    var stack = [];

    var tot = document.getElementsByClassName("Bars");

    // Adding the entire initial array as an "unsorted subarray"
    stack.push(0);
    stack.push(arr.length - 1);

    // There isn't an explicit peek() function
    // The loop repeats as long as we have unsorted subarrays
    while (stack[stack.length - 1] >= 0) {
      // Extracting the top unsorted subarray
      var end = stack.pop();
      var start = stack.pop();

      for (var i = start; i <= end; i++) {
        tot[i].style["background-color"] = "turquoise";
      }

      await waitforme(20);

      var pivotIndex = await partition(arr, start, end);

      await waitforme(20);

      for (var i = start; i <= end; i++) {
        tot[i].style["background-color"] = "brown";
      }

      // If there are unsorted elements to the "left" of the pivot,
      // we add that subarray to the stack so we can sort it later
      if (pivotIndex - 1 > start) {
        stack.push(start);
        stack.push(pivotIndex - 1);
      }

      // If there are unsorted elements to the "right" of the pivot,
      // we add that subarray to the stack so we can sort it later
      if (pivotIndex + 1 < end) {
        stack.push(pivotIndex + 1);
        stack.push(end);
      }
    }
    setSorting(false);
  }

  async function partition(arr, start, end) {
    // Taking the last element as the pivot
    var tot = document.getElementsByClassName("Bars");
    const pivotValue = arr[end];
    let pivotIndex = start;
    for (let i = start; i < end; i++) {
      tot[i].style["backgroud-color"] = "green";
      tot[pivotIndex].style["background-color"] = "green";
      await waitforme(30);
      if (arr[i] < pivotValue) {
        // Swapping elements
        [arr[i], arr[pivotIndex]] = [arr[pivotIndex], arr[i]];
        tot[i].style["height"] = arr[i] * 5 + "px";
        tot[pivotIndex].style["height"] = arr[pivotIndex] * 5 + "px";
        // Moving to next element
        await waitforme(30);
        tot[i].style["background-color"] = "turqouise";
        tot[pivotIndex].style["background-color"] = "turquoise";
        pivotIndex++;
      }
      await waitforme(30);
      tot[i].style["background-color"] = "turqouise";
      tot[pivotIndex].style["background-color"] = "turquoise";
    }

    // Putting the pivot value in the middle
    [arr[pivotIndex], arr[end]] = [arr[end], arr[pivotIndex]];
    tot[end].style["height"] = arr[end] * 5 + "px";
    tot[pivotIndex].style["height"] = arr[pivotIndex] * 5 + "px";

    return pivotIndex;
  }

  async function MergeSort() {
    setSorting(true);
    mergeSort(0, arr.length - 1);
    setSorting(false);
  }

  async function mergeSort(l, r) {
    if (l >= r) {
      return;
    }
    const m = l + Math.floor((r - l) / 2);
    await mergeSort(l, m);
    await mergeSort(m + 1, r);
    await Merge(l, m, r);
  }

  async function Merge(low, mid, high) {
    var tot = document.getElementsByClassName("Bars");
    var n1 = mid - low + 1;
    var n2 = high - mid;
    var left = new Array(n1);
    var right = new Array(n2);
    for (var i = 0; i < n1; i++) {
      await waitforme(10);
      tot[low + i].style["background-color"] = "turquoise";
      left[i] = arr[low + i];
    }
    for (var i = 0; i < n2; i++) {
      await waitforme(10);
      tot[mid + i + 1].style["background-color"] = "green";
      right[i] = arr[mid + i + 1];
    }

    await waitforme(10);
    let a = 0,
      b = 0,
      c = low;

    while (a < n1 && b < n2) {
      if (left[a] <= right[b]) {
        if (n1 + n2 === tot.length) {
          tot[c].style["background-color"] = "brown";
        } else {
          tot[c].style["background-color"] = "pink";
        }
        tot[c].style["height"] = left[a] * 5 + "px";
        arr[c] = left[a];
        c++;
        a++;
      } else {
        if (n1 + n2 === tot.length) {
          tot[c].style["background-color"] = "brown";
        } else {
          tot[c].style["background-color"] = "pink";
        }
        tot[c].style["height"] = right[b] * 5 + "px";
        arr[c] = right[b];
        c++;
        b++;
      }
    }
    while (a < n1) {
      await waitforme(10);
      if (n1 + n2 === tot.length) {
        tot[c].style["background-color"] = "brown";
      } else {
        tot[c].style["background-color"] = "pink";
      }
      tot[c].style["height"] = left[a] * 5 + "px";
      arr[c] = left[a];
      c++;
      a++;
    }
    while (b < n2) {
      await waitforme(10);
      if (n1 + n2 === tot.length) {
        tot[c].style["background-color"] = "brown";
      } else {
        tot[c].style["background-color"] = "pink";
      }
      tot[c].style["height"] = right[b] * 5 + "px";
      arr[c] = right[b];
      c++;
      b++;
    }
  }

  return (
    <div className="row">
      <div className=" con">{arr.map(inputarray)}</div>
      <div className="col less">
        <div className="buttons">
          <input
            type="range"
            min="5"
            max="100"
            onChange={isSorting ? null : handleChange}
          />
          <button onClick={isSorting ? null : Generate}>Random Array</button>
          <button onClick={isSorting ? null : Insertionsort}>
            Insertion Sort
          </button>
          <button onClick={isSorting ? null : BubbleSort}>Bubble Sort</button>
          <button onClick={isSorting ? null : QuickSort}>Quick Sort</button>
          <button onClick={isSorting ? null : MergeSort}>Merge Sort</button>
        </div>
      </div>
    </div>
  );
}

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

export default Sortvis;
